import Vue from 'vue';
import Component from 'vue-class-component';

@Component
export class TagHelper extends Vue{
  createTag(){
    const name = window.prompt('');
    if (!name) {
      return window.alert('');
    }else if(name.length>4){
      return window.alert('4')
    }
    this.$store.commit('addTag', name);
  }
}

export default TagHelper;